import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { Support } from "../_models/index";

@Injectable()
export class SupportService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    getAll() {
        return this.http.get(this.baseUrl._baseUrl + 'support/getall', this.jwt()).map((response: Response) => response.json());
    }
    getById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'support/info/' + id, this.jwt()).map((response: Response) => response.json());
    }

    priorityUpdate(sp_id, id: number) {
        return this.http.post(this.baseUrl._baseUrl + 'support/priority/'+sp_id, id,this.jwt()).map((response: Response) => response.json());
    }

    sendMessage(sp_id, data: any) {
        return this.http.post(this.baseUrl._baseUrl + 'support/sendMessage/'+sp_id, data,this.jwt()).map((response: Response) => response.json());
    }

   /* searching(user) {
        return this.http.post(this.baseUrl._baseUrl + 'support/search', JSON.stringify(user), this.jwt()).map((response: Response) => response.json());
    }*/

    updateStatus(id, status: any) {
        return this.http.post(this.baseUrl._baseUrl + 'support/status/'+id, status ,this.jwt()).map((response: Response) => response.json());

    }
    assignTo(data: any) {
        return this.http.post(this.baseUrl._baseUrl + 'support/assignTo',data, this.jwt()).map((response: Response) => response.json());

    }

    getChat(data: any){
        return this.http.post(this.baseUrl._baseUrl + 'support/getChat',data, this.jwt()).map((response: Response) => response.json());
    }
    // private helper methods

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
